.woocommerce-layout {
  margin: 0;
  padding: 0; }

.woocommerce-layout__primary {
  margin: 80px 0 0 40px;
  margin: 80px 0 0 var(--large-gap); }
  @media (min-width: 961px) {
    .woocommerce-layout__primary {
      margin-top: 100px; } }
  @media (max-width: 782px) {
    .woocommerce-layout__primary {
      margin-top: 60px; } }

.woocommerce-layout .woocommerce-layout__main {
  padding-right: 40px;
  padding-right: var(--large-gap);
  max-width: 100%; }

.woocommerce-page .update-nag {
  display: none; }

.woocommerce-admin-is-loading #adminmenumain,
.woocommerce-admin-is-loading #wpfooter,
.woocommerce-admin-is-loading #wpadminbar,
.woocommerce-admin-is-loading .woocommerce-layout__header,
.woocommerce-admin-is-loading .update-nag,
.woocommerce-admin-is-loading .woocommerce-store-alerts,
.woocommerce-admin-is-loading .woocommerce-message,
.woocommerce-admin-is-loading .notice,
.woocommerce-admin-is-loading .error,
.woocommerce-admin-is-loading .updated {
  display: none; }

.woocommerce-admin-full-screen {
  background: #f6f7f7;
  color: #50575e;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  /* Hide wp-admin and WooCommerce elements when the dashboard body class is present */ }
  .woocommerce-admin-full-screen #wpbody-content {
    min-height: 100vh; }
  .woocommerce-admin-full-screen #adminmenumain,
  .woocommerce-admin-full-screen .woocommerce-layout__header,
  .woocommerce-admin-full-screen .update-nag,
  .woocommerce-admin-full-screen .woocommerce-store-alerts,
  .woocommerce-admin-full-screen .woocommerce-message,
  .woocommerce-admin-full-screen .notice,
  .woocommerce-admin-full-screen .error,
  .woocommerce-admin-full-screen .updated {
    display: none; }
  .woocommerce-admin-full-screen #wpcontent {
    margin-left: 0; }
